.has-content-tiles{
	+ .footer-outer-wrap{
		margin-top: 0;
	}
}
.c-content-tiles{
	background: #fff no-repeat center center / cover;
	position: relative;

	@extend .slick-dots-sty-1;
	@extend .slick-dots-sty--black;
	
	.content-tile-bg-img-carousel{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		z-index: 0;

		*{
			height: 100%;
		}

		.img-item,
		.img{
			width: 100%;
			height: 100%;
		}
		.img{
			background: #fff no-repeat center center / cover;
		}
	}

	.slick-dots{
		margin-top: 30px;
	}

	.o-container{
		z-index: 1;
	}

	.bg-img{
		position: absolute;
		z-index: 0;
		left: 0;
		top: 0;
		width: 60%;
		height: 100%;
		background: #fff no-repeat center center / cover;

		@media (max-width: 767px){
			display: none;
		}
	}
	&.img--cover{
		.bg-img{
			width: 100%;
		}
	}
	&.img--right,
	&.img--left{
		.bg-img{
			// background-size: 60% 100%;
			// background-position: left center;
		}
	}
	&.img--right{
		.bg-img{
			right: 0;
			left: auto;
		}

		&.img-shade--light-grey,
		&.img-shade--green,
		&.img-shade--red,
		&.img-shade--white{
			&:before{
				right: auto;
				left: 0;
				background: linear-gradient(to right, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 55%,rgba(255,255,255,0) 100%);
			}
		}
		&.img-shade--red{
			&:before{
				background: linear-gradient(to right, rgba(125,24,33,1) 0%,rgba(96,23,27,1) 55%,rgba(83,22,24,0) 100%);
			}
		}
		&.img-shade--green{
			&:before{
				background: linear-gradient(to right, rgba(24,67,77,1) 0%,rgba(25,20,44,1) 40%,rgba(35,25,55,1) 55%,rgba(52,34,74,0) 100%);
			}
		}

		&.img-shade--light-grey{
			&:before{
				background: url('../img/tiles-bg/tile-grey-overlay.png') no-repeat center center / cover;
			}
		}
	}

	&.img-shade--light-grey,
	&.img-shade--green,
	&.img-shade--red,
	&.img-shade--white{
		&:before{
			content: '';
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			width: 75%;
			height: 100%;
			background: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 30%,rgba(255,255,255,1) 100%);
			z-index: 1;

			@media (max-width: 767px){
				display: none;
			}
		}
	}

	&.img-shade--red{
		&:before{
			background: linear-gradient(to right, rgba(83,22,24,0) 0%,rgba(96,23,27,1) 30%,rgba(125,24,33,1) 100%);
		}
	}

	&.img-shade--green{
		&:before{
			background: linear-gradient(to right, rgba(52,34,74,0) 0%,rgba(35,25,55,1) 20%,rgba(25,20,44,1) 30%,rgba(24,67,77,1) 100%);
		}
	}

	&.img-shade--light-grey{
		&:before{
			width: 100%;
			background: url('../img/tiles-bg/tile-rey-overlay--reverse.png') no-repeat center center / cover;
		}
	}


	.wrap-col{
		&.wc--sty-1{
			padding-left: 250px;
			@media (max-width: 1360px){
				padding-left: 80px;
			}
		}
	}
	
	&.txt--right{
		.wrap-col{
			margin: 0 0 0 auto;
			padding-left: 60px;
		}
	}

	&.txt--left{
		.wrap-col{
			&.wc--sty-1{
				padding-left: 250px;
				@media (max-width: 1360px){
					padding-left: 80px;
				}
			}
		}
	}
	
	&.txt--center{
		text-align: center;
		.wrap-col{
			text-align: center;
			margin: 0;
			width: 100%;
			justify-content: center;
			.sec-logo{
				text-align: center;
				img{
					margin-right: auto;
					margin-left: auto;
				}
			}
		}
	}
	
	&.txt--white{
		color: #fff;

		.txt-block h2.h2-sty-1{
			color: #fff;
		}

		.action-block{
			.c-btn-white.btn--play-1{
				border: none;
			}
			.c-btn-glow.sty--a{
				color: #fff;
				border-color: #fff;

				&:hover{
					@media (min-width: 1025px){
						border-color: transparent;
					}
				}
			}
		}
	}
	&.txt--black{
		color: #222;

		.txt-block h2.h2-sty-1{
			color: #222;
		}

		.action-block{
			.c-btn-white.btn--play-1{
				border: none;
			}
			.c-btn-glow.sty--a{
				color: #222;
				border-color: #222;

				&:hover{
					@media (min-width: 1025px){
						border-color: transparent;
						color: #fff;
					}
				}
			}
		}
	}

	&.tile--highlight{
		.txt-block{
			max-width: 520px;

			h1{
				font-size: 60px;
			}
		}
		.wrap-col{
			align-items: center;
			@media (min-width: 768px){
				padding-top: 20px;
				padding-bottom: 20vh;
			}
		}
	}

	&.tile--focus{
		text-align: center;

		.wrap-col{
			text-align: center;
			padding-right: 0;
			padding-left: 0;
			width: 100%;
			max-width: 660px;
			margin: 0 auto;

			@media (max-width: 767px){
				padding-top: 40px !important;
			}
		}
		.txt-block{
			max-width: none;
		}
		.sec-logo{
			img{
				margin: 0 auto;
			}
		}
	}

	&, &.tile--highlight{
		.txt-block{
			&.tb-size-1{
				max-width: 580px;
			}
		}
	}

	.o-container{
		position: relative;
	}
	.img-sec{
		position: absolute;
		top: 50%;
		transform: translate(-50%, -50%);
		left: 25%;
		max-width: 45%;
	}

	.wrap-col{
		width: 50%;
		display: flex;
		align-items: center;
		min-height: 75vh;
		padding: 12.5vh 0;
		height: calc(100vh - 85px);

		@media (max-width: 1800px){
			height: calc(100vh - 83px);
		}
		@media (max-width: 1600px){
			height: calc(100vh - 82px);
		}
	}

	.txt-block{
		width: 100%;
		max-width: 465px;

		&.block--width-lg{
			max-width: 600px;
		}

		h1{
			font-size: 43px;
			line-height: 1.2em;
			margin: 0 0 25px 0;
			font-weight: bold;

			& + h2{
				margin-top: -15px;
			}
		}
		h2{
			font-size: 28px;
			line-height: 1.2em;
			margin: 0 0 25px 0;

			&.h2-sty-1{
				font-size: 24px;
				color: #505459;
				line-height: 1em;
				margin-bottom: 10px;
				text-transform: uppercase;
			}
		}
	}

	.sec-logo{
		margin-bottom: 35px;
		img{
			display: block;
		}
	}

	.action-block{
		vertical-align: top;
		margin-top: 35px;

		> *{
			vertical-align: top;
		}

		.c-btn-white:not(.btn--play-1), .c-btn-glow{
			min-width: 200px;
		}

		a + a{
			margin-left: 40px;
		}
		
		.c-btn-white.btn--play-1{
			border: 1px solid #000;
		}
	}
}

.c-offers{
	margin: 80px 0;
	
	&.offers--sty-1{
		.offers-list{
			max-width: 1235px;
			border-radius: 0px;
			margin: 0 auto;
			
			.item{
				padding: 0 10px;
				img{
					display: block;
					margin: 0 auto;
				}
				&:hover{
					// @media (min-width: 1025px){
						.img{
							.overlay-txt{
								opacity: 1;
							}
						}
					// }
				}
			}
			.img{
				position: relative;
				max-width: 230px;
				margin: 0 auto 10px;
				border-radius: 8px;
				overflow: hidden;
				
				.overlay-txt{
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					overflow-x: hidden;
					overflow-y: auto;
					background: rgba(95,57,145,.95);
					color: #fff;
					padding: 20px;
					opacity: 0;
					transition: all .35s;
					margin: 0;
					line-height: 1.4em;
				}
			}
			.title{
				text-align: center;
				margin: 0;
			}
		}
	}
	
	.heading-sec{
		text-align: center;
		margin-bottom: 50px;
		
		h1{
			font-size: 45px;
			line-height: 1.2em;
			margin: 0 0 25px 0;
			font-weight: bold;
		}
		p{
			max-width: 560px;
			margin: 0 auto;
		}
	}
	.offers-list{
		max-width: 1000px;
		margin: 0 auto;
		border-radius: 8px;
		overflow: hidden;
		position: relative;
		img{
			display: block;
			transition: transform .35s;
			transform: scale(1);
		}
		.item{
			overflow: hidden;
			@media (min-width: 768px){
				&:hover{
					@media (min-width: 1025px){
						// a{
							img{
								transform: scale(1.1) rotateZ(1.5deg);
							}
						// }
					}
				}
			}
		}
	}
	.action{
		text-align: center;
		margin-top: 50px;
		a{
			min-width: 200px;
		}
	}
}

.c-content-block{
	margin-top: 100px;
	margin-bottom: 100px;

    &, .img-txt-block{
        clear: both;
        &:after{
            content: "";
            clear: both;
            display: table;
        }
    }

	h1{
		font-size: 25px;
		line-height: 1em;
		text-transform: uppercase;
		margin-bottom: 30px;
		font-family: 'arquitecta_mediummedium';

		@media (max-width: 767px){
			font-size: 19px;
			line-height: 1em;
		}
	}

	.img-txt-block{
		.img{
			float: left;
			width: 480px;
			border-radius: 8px;
			overflow: hidden;
			position: relative;

			img{
				display: block;
				width: 100%;
				height: auto;
			}
		}
		.txt{
			margin-left: 560px;
			max-width: 625px;
			@media (max-width: 1100px){
				margin-left: 530px;
			}

			h2{
				font-size: 42px;
				line-height: 1.3em;
				font-weight: bold;
				margin: 0 0 20px 0;
			}
			.action{
				margin-top: 35px;
				a{
					min-width: 200px;
				}
			}
		}
	}
}

.c-exp-highlight{
	padding: 60px 0;

	> .o-container{
		@extend .height-cover;
		display: flex;
		flex-direction: column;
    	justify-content: center;
	}

	.heading-txt{
		text-align: center;
		max-width: 820px;
		margin: 0 auto 50px auto;

		p{
			max-width: 560px;
			margin-right: auto;
			margin-left: auto;
		}
	}

	h1{
		@extend .heading-sty-4;
	}

	.exp-list{
		margin: 0 -10px;
		.item{
			.wrap{
				display: block;
				position: relative;
				margin: 0px 10px;
				border-radius: 10px;
				overflow: hidden;

				.img{
					border-radius: 10px;
					display: block;
					padding-top: 170%;
					width: 100%;
					background: #000 no-repeat center center;
					background-size: cover;
					transform: scale(1) rotateZ(0deg);
					transition: all .35s ease-in-out;
				}
				@media (min-width: 1025px){
					&:hover{
						.img{
							transform: scale(1.1) rotateZ(2deg);
						}
					}
				}
			}

			.this-logo{
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				padding-bottom: 40px;
				text-align: center;

				img{
					display: block;
					margin: 0 auto;
				}
			}
		}
	}
}

// Responsive 
