	.c-content-tiles.tile--highlight .txt-block h1{
		font-size: 50px;
	}
	.c-content-tiles .txt-block h2{
		font-size: 25px;
		margin-bottom: 20px;
	}
	.c-offers .heading-sec h1, .c-content-block .img-txt-block .txt h2,
	.c-content-tiles .txt-block h1{
		font-size: 40px;
		margin-bottom: 25px;
	}
	.c-content-tiles .action-block a+a{
		margin-left: 30px;
	}
}
