		margin: 0 -5px;
	}
	.c-offers.offers--sty-1 .offers-list .item{
		padding: 0 5px;
	}
	.c-content-tiles .txt-block h2.h2-sty-1{
		font-size: 22px;
	}
	.c-content-tiles .sec-logo img{
		max-height: 65px;
		max-width: 300px;
	}
	.c-offers .heading-sec h1, .c-content-block .img-txt-block .txt h2,
	.c-content-tiles .txt-block h1{
		font-size: 30px;
		margin-bottom: 17px;
	}
	.c-content-tiles .txt-block h2 {
	    font-size: 20px;
	    margin-bottom: 15px;
	}
	.c-content-tiles.tile--highlight .txt-block h1{
		font-size: 40px;
		margin-bottom: 28px;
	}
}
@media (max-width: 1024px){
	.c-offers.offers--sty-1 .offers-list .slick-current .img .overlay-txt{
		// opacity: 1;
	}
}
@media (max-width: 1023px){
	.c-content-block .img-txt-block .img{
		width: 340px;
	}
	.c-content-block .img-txt-block .txt{
		margin-left: 370px;
	}
	.c-content-tiles .img-sec{
		max-width: 40%;
	}
	.c-content-block{
		margin-top: 70px;
		margin-bottom: 70px;
	}
}
@media (max-width: 920px){
	.c-content-tiles .txt-block h2.h2-sty-1{
		font-size: 18px;
	}
	.c-content-tiles:nth-child(2n+1) .wrap-col{
		padding-right: 0;
	}
	.c-content-tiles:nth-child(even) .wrap-col{
		padding-left: 0;
	}
	.c-content-tiles.tile--highlight .txt-block h1{
		font-size: 35px;
	}
	.c-offers .heading-sec h1, .c-content-block .img-txt-block .txt h2,
	.c-content-tiles .txt-block h1{
		font-size: 26px;
	}
	.c-content-tiles .txt-block h2{
		font-size: 18px;
	}
	.c-content-tiles .action-block a+a{
		margin-left: 20px;
	}
	.c-content-tiles .wrap-col{
		min-height: 560px;
		padding-top: 75px;
		padding-bottom: 75px;
	}
	.c-offers.offers--sty-1 .offers-list .title{
		font-size: 15px;
	}
}
@media (max-width: 767px){
	.c-content-tiles:first-child() .txt-block h1{
		font-size: 32px;
		font-weight: bold;
	}
	.c-offers.offers--sty-1 .offers-list .slick-slide .item .img .overlay-txt{
		opacity: 0;
	}
	.c-offers.offers--sty-1 .offers-list .slick-slide.slick-current .item:hover .img .overlay-txt{
		opacity: 1;
	}
	.c-offers.offers--sty-1 .offers-list{
	    max-width: 250px;
	    margin: 0 auto;
	}
	.c-offers.offers--sty-1 .offers-list .title{
		font-size: 14px;
	}
	.c-content-tiles .img-sec{
		img{
			max-height: 23vh;
			margin: 0 auto;
		}
	}
	.c-offers{
		margin-top: 60px;
		margin-bottom: 60px;
	}
	.c-content-block .img-txt-block .img{
		float: none;
		width: 100%;
		max-width: 480px;
		margin-right: auto;
		margin-left: auto;
		margin-bottom: 20px;
	}
	.c-content-block .img-txt-block .txt{
		margin-left: 0;
	}
	.c-content-tiles .wrap-col.wc--sty-1, .c-content-tiles:nth-child(2n) .wrap-col.wc--sty-1{
		padding-left: 0;
	}
	.c-content-tiles .txt-block h2 {
	    font-size: 24px;
	}
	.c-content-tiles .img-sec {
	    max-width: 62%;
	    top: 23%;
	    left: 50%;
	    width: 100%;
	}

	.c-content-tiles{
		background-position: center top;
		background-size: 100% auto;
		background-color: #fff;

		&.img--cover{
			background-size: cover;
		}

		.wrap-col{
			position: relative;
			&:before{
				content:'';
				display: none;
				position: absolute;
				bottom: 0;
				left: 0;
				height: 70%;
				width: 100%;
				z-index: 1;
				margin: 0 -15px;
				padding: 0 15px;
				box-sizing: content-box;
				background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 30%);
			}
			.txt-block{
				position: relative;
				z-index: 2;
			}
		}

		&.img-shade--white{
			.wrap-col{
				&:before{
					display: block;
				}
			}
		}

		&.img-shade--red{
			.wrap-col{
				&:before{
					display: block;
					background: linear-gradient(180deg,rgba(113,32,51,0) 0%, #712033 25%);
				}
			}
		}

		&.img-shade--light-grey{
			.wrap-col{
				&:before{
					display: block;
					background: linear-gradient(to bottom, rgba(68,64,79,0) 0%,rgba(55,50,69,1) 25%,rgba(32,23,51,1) 100%);
				}
			}
		}

		&.img-shade--green{
			.wrap-col{
				&:before{
					display: block;
					background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(27,43,61,1) 25%);
				}
			}
		}
	}
	.c-content-tiles .wrap-col{
		width: 100%;
		padding-top: calc(50vh - 66px);
		min-height: calc(100vh - 66px);
		height: auto;
		padding-bottom: 40px;
		padding-right: 0;
		padding-left: 0;
		display: flex;
		align-items: center;
		
		.hg-650 &{
			padding-top: calc(50vh - 58px);
			height: calc(100vh - 58px);
		}
	}

	.c-content-tiles.tile--highlight .wrap-col{
		align-items: center;
	}

	.c-content-tiles:nth-child(2n+1) .wrap-col{
		padding-left: 0;
	}

	.c-content-tiles.tile--focus .wrap-col{
		padding-top: 40px;
	}

	.c-content-tiles .sec-logo img {
	    max-height: 50px;
	    max-width: 240px;
	}

	.c-content-tiles.tile--highlight .txt-block h1{
		font-size: 32px;
		font-weight: bold;
	}

